import { Block, BlockChildren, Data } from '@ali/4ever-cangjie';
import { ELEMENT_TYPE } from '../../utils/meta';

export interface CardData<T = Record<string, any>> extends Data {
  readonly cardType?: string;
  readonly height?: number;
  readonly spacing?: {
    readonly before?: number;
    readonly after?: number;
  };
  readonly metadata?: T;
}

type CardChildren = BlockChildren;

export interface CardProperties<D = CardData, N extends BlockChildren = CardChildren> {
  data?: D;
  nodes?: N;
}

export default class Card extends Block<CardData, CardChildren> {
  static TYPE = ELEMENT_TYPE;

  static create<SD = CardData, SN extends BlockChildren = CardChildren>(
    properties: Card | CardProperties<SD, SN> | string,
  ) {
    if (typeof properties === 'string') {
      throw new Error('`Card.create` does not accept string');
    }
    if (Card.isCard(properties)) {
      return properties as Block<SD, SN>;
    }
    return super.create<SD, SN>({
      ...properties,
      type: Card.TYPE,
    });
  }

  static isCard(maybeCard: any): maybeCard is Card {
    return Block.isBlock(maybeCard) && maybeCard.type === Card.TYPE;
  }
}
